import React, { useContext } from 'react'
import ipfsContext from '../../context/ipfs.jsx'

const IpfsFileList = (props) => {
  const { files = [] } = props
  const { ipfs } = useContext(ipfsContext)
  const { protocol, host, port, 'api-path': apiPath } = ipfs.getEndpointConfig()
  return (
    <ul>
      {
        !files.length
          ? <li>No files.</li>
          : files.map((file, index) => (
            <li key={index}><a href={`${protocol}://${host}:${port}${apiPath}cat?arg=${file.hash}`} target='_blank' rel='noopener noreferrer' >{file.path}</a> - {file.hash} - {file.size} bytes</li>
          ))}
    </ul>
  )
}

export default IpfsFileList
