import React, { useState, useContext, useEffect, createElement } from 'react'
import ethereumContext from '../../../context/ethereum.jsx'
import ObjectPropertyList from '../../ObjectPropertyList.jsx'

const WhisperMessageList = (props) => {
  const { showSymKey = false, msgComponent = null } = props
  const { shh, addShhError, utils } = useContext(ethereumContext)
  const [symKeyId, setSymKeyId] = useState('')
  const [privateKeyId, setPrivateKeyId] = useState('')
  const [subscription, setSubscription] = useState(null)
  const [messages, setMessages] = useState([])

  useEffect(() => {
    const addMessage = (message) => { setMessages((messages) => [...messages, message]) }
    const subscribe = (symKeyId, privateKeyId) => (shh.subscribe('messages', { symKeyId, privateKeyId }).on('data', addMessage).on('error', addShhError))

    if (!subscription) {
      if (symKeyId) {
        setSubscription(subscribe(symKeyId, null))
      } else if (privateKeyId) {
        setSubscription(subscribe(null, privateKeyId))
      }
    }
    return () => {
      if (subscription) {
        subscription.unsubscribe()
        setSubscription(null)
      }
    }
  }, [subscription, shh, addShhError, symKeyId, privateKeyId, utils])

  return (
    <>
      <label>Key id: </label>
      {showSymKey && <input type='text' placeholder='Symmetric Key Id' onChange={(event) => setSymKeyId(event.target.value)} />}
      <input type='text' placeholder='Private Key Id' onChange={(event) => setPrivateKeyId(event.target.value)} />
      <ul>
        {!messages.length
          ? <li>No messages.</li>
          : messages.map((message, index) => (
            msgComponent
              ? createElement(msgComponent, { message, key: index })
              : <ul key={index}><ObjectPropertyList object={message} /></ul>
          ))}
      </ul>
    </>
  )
}

export default WhisperMessageList
