import React, { useContext, useState, useEffect } from 'react'
import ethereumContext from '../../../context/ethereum.jsx'
import contractContext from '../../../context/contract.jsx'
import ErrorList from '../../ErrorList.jsx'
import ObjectPropertyList from '../../ObjectPropertyList.jsx'
import { valueAdder, errorAdder } from '../../../utils/context.js'
import { methodExists, methodIsPayable, getParams } from '../../../utils/abiModel.js'

const EthereumContractMethodForm = (props) => {
  const { method = null, sendTx = false } = props
  const { authContract, addContractError } = useContext(contractContext)
  const { authEthAccounts, utils } = useContext(ethereumContext)
  const abiModel = authContract.jsonInterface
  const [form, setForm] = useState([])
  const [payable, setPayable] = useState(false)
  const [value, setValue] = useState(0)
  const [receipts, setReceipts] = useState([])
  const [formErrors, setFormErrors] = useState([])
  const [invalidForm, setInvalidForm] = useState(false)
  const addReceipt = valueAdder(setReceipts)
  const updateForm = (event, name) => {
    event.stopPropagation()
    event.preventDefault()
    event.persist()
    setForm((form) => {
      const newForm = [...form]
      const index = form.findIndex((field) => (name === field.name))
      newForm[index] = { ...newForm[index], value: event.target.value }
      return newForm
    })
  }

  const addFormError = errorAdder(setFormErrors)
  const callMethod = (params, options) => authContract.methods[method](...params).call(options)
  const sendMethod = (params, options) => authContract.methods[method](...params).send(options)
  const execMethod = async (event) => {
    event.stopPropagation()
    event.preventDefault()
    try {
      setFormErrors([])
      const params = Object.values(form).map((field) => (field.value))
      const options = { from: authEthAccounts[0], value: utils.toWei(value, 'ether') }
      if (sendTx) {
        addReceipt(await sendMethod(params, options))
      } else {
        addReceipt(await callMethod(params, options))
      }
    } catch (error) {
      if (error.code === 'INVALID_ARGUMENT') {
        addFormError(error)
      } else {
        addContractError(error)
      }
    }
  }

  useEffect(() => {
    if (!invalidForm) {
      const validMethod = methodExists(abiModel, method)
      if (!validMethod) {
        addFormError(new Error(`Contract json interface does not contain provided method: ${method}`))
        setInvalidForm(true)
      } else if (!form.length) {
        const params = getParams(abiModel, method)
        setForm(params)
        if (methodIsPayable(abiModel, method)) {
          setPayable(true)
        }
      }
    }
  }, [invalidForm, abiModel, method, form.length, addFormError])

  if (invalidForm) { return null }

  return (
    <>
      <h3>{sendTx ? 'Send' : 'Call'} <pre>{method}</pre> Form </h3>
      <form>
        { form.map(({ name, type }, index) => (
          <label key={index}>
            {name} ({type}) {}
            <input name={name} value={form[index].value} placeholder={name} onChange={(event) => updateForm(event, name)} type='text' />
            <br />
          </label>
        )) }
        {payable
          ? (
            <label>
            Payable value (ETH) {}
              <input type='number' value={value} onChange={(event) => { event.persist(); setValue(event.target.value) }} />
            </label>
          )
          : null
        }
        <button onClick={execMethod}>Submit</button>
      </form>
      { formErrors.length ? <ErrorList errors={formErrors} /> : null }
      { receipts.length
        ? <ol>{ receipts.slice().reverse().map((receipt) => <li><ObjectPropertyList object={receipt} name='Receipt' /></li>) }</ol>
        : null }
    </>
  )
}

export default EthereumContractMethodForm
