import React, { createContext, useState, useEffect } from 'react'
import Web3 from 'web3'
import * as Web3Utils from 'web3-utils'
import ErrorList from '../components/ErrorList.jsx'
import { handleErrorStub, isFalseyOrPromise } from '../utils/context.js'

const context = createContext()

export const EthereumProvider = (props) => {
  const { authConfig, eventConfig, shhConfig } = props
  const [authEth, setAuthEth] = useState(null)
  const [authEthAccounts, setAuthEthAccounts] = useState('')
  const [eventEth, setEventEth] = useState(null)
  const [shh, setShh] = useState(null)
  const [ethErrors, setEthErrors] = useState([])
  const [shhErrors, setShhErrors] = useState([])
  const addEthError = (error) => setEthErrors((errors) => { console.error(error); return [...errors, error] })
  const addShhError = (error) => setShhErrors((errors) => { console.error(error); return [...errors, error] })

  useEffect(() => {
    const setupAuthEth = async () => setAuthEth(await authEthInstance(authConfig, setAuthEthAccounts, addEthError))
    const setupEventEth = async () => setEventEth(await eventEthInstance(eventConfig, addEthError))
    const setupShh = async () => setShh(await shhInstance(shhConfig, addShhError))
    if (!ethErrors.length && !authEth) {
      setAuthEth(setupAuthEth())
    }
    if (!ethErrors.length && !eventEth) {
      setEventEth(setupEventEth())
    }
    if (!shhErrors.length && !shh) {
      setShh(setupShh())
    }
  }, [ethErrors.length, authEth, eventEth, shhErrors.length, shh, eventConfig, shhConfig, authConfig])

  const errors = []
  if ((isFalseyOrPromise(authEth) || isFalseyOrPromise(eventEth)) && ethErrors.length) { errors.push(<pre>EthereumProvider Eth Error: <ErrorList errors={ethErrors} /></pre>) }
  if (isFalseyOrPromise(shh) && shhErrors.length) { errors.push(<pre>EthereumProvider Shh Error: <ErrorList errors={shhErrors} /></pre>) }
  if (errors.length) { return errors }

  if (isFalseyOrPromise(authEth) || isFalseyOrPromise(eventEth) || isFalseyOrPromise(shh)) { return <pre>Connecting to Ethereum...</pre> }

  return (
    <context.Provider value={{ authEth, authEthAccounts, eventEth, shh, setAuthEth, setEventEth, setShh, ethErrors, shhErrors, addEthError, addShhError, utils: Web3Utils }}>
      {props.children}
    </context.Provider>
  )
}

const authEthInstance = async ({ net = null, options = {} }, accountsHandler = () => {}, errorHandler = handleErrorStub) => {
  const { ethereum } = window
  if (!ethereum) {
    errorHandler(new Error('Looks like you need a Dapp browser to get started.'))
  } else {
    try {
      accountsHandler(await ethereum.enable())
      ethereum.on('accountsChanged', (accounts) => accountsHandler(accounts))
      return new Web3.modules.Eth(ethereum, net, options)
    } catch (error) {
      if (error.code === 4001) {
        errorHandler(new Error('You need to sign into your Ethereum provider.'))
      } else {
        errorHandler(error)
      }
    }
  }
}

const eventEthInstance = ({ provider, net = null, options = {} }, errorHandler = handleErrorStub) => {
  try {
    return new Web3.modules.Eth(provider, net, options)
  } catch (error) {
    errorHandler(error)
  }
}

const shhInstance = ({ provider, net = null, options = {} }, errorHandler = handleErrorStub) => {
  try {
    return new Web3.modules.Shh(provider, net, options)
  } catch (error) {
    errorHandler(error)
  }
}

export const EthereumConsumer = context.Consumer

export default context
