import React, { useContext, useState, useEffect, createElement } from 'react'
import contractContext from '../../../context/contract.jsx'
import { fixReturnValues, handleErrorStub } from '../../../utils/context.js'
import ObjectPropertyList from '../../ObjectPropertyList.jsx'

const EthereumContractEventList = (props) => {
  const { events, eventComponent = null } = props
  const { eventContract, addContractError } = useContext(contractContext)
  const [subscriptions, setSubscriptions] = useState([])
  const [contractEvents, setContractEvents] = useState([])
  const addContractEvent = (event) => setContractEvents((events) => [...events, event])

  useEffect(() => {
    if (!subscriptions.length) {
      setSubscriptions(subscribe(eventContract, events, addContractEvent, addContractError))
    }
    return () => unsubscribe(subscriptions)
  }, [events, eventContract, subscriptions, addContractError])

  return (
    <ol>
      {
        !contractEvents.length
          ? <li>No events.</li>
          : contractEvents.slice().reverse().map((event, index) => (
            eventComponent
              ? createElement(eventComponent, { event, key: index })
              : <li key={index}>
                {event.event}
                <ObjectPropertyList object={event.returnValues} name='' />
              </li>
          ))
      }
    </ol>
  )
}

// todo: store events in local storage?
const subscribe = (contract, events, eventAdder, errorHandler = handleErrorStub) => {
  const eventHandler = (event) => {
    if (event && event.returnValues) {
      eventAdder({ ...event, returnValues: fixReturnValues(event.returnValues) })
    } else {
      eventAdder(event)
    }
  }

  // todo: determine now (blocknumber)
  // todo: retrieve list of events until now

  // if no events passed, add a single listener for all events
  if (!events) {
    return [
      contract.events.allEvents({})
        .on('data', eventHandler)
        .on('changed', console.info) // todo: add logic for removing event from local storage
        .on('error', errorHandler)
    ]
  }

  // add a listener per event provided
  return Object.entries(events).map(([name, options]) => {
    return contract.events[name](options)
      .on('data', eventHandler)
      .on('changed', console.info) // todo: add logic for removing event from local storage
      .on('error', errorHandler)
  })
}

const unsubscribe = (subscriptions) => {
  if (Array.isArray(subscriptions)) {
    subscriptions.forEach((subscription) => subscription.unsubscribe())
  }
}

export default EthereumContractEventList
