// in web3 docs, but missing export? Still seems to be used for event listener ðŸ¤®
export const isBigNumber = object => object && object.constructor && object.constructor.isBigNumber && object.constructor.isBigNumber(object)
// might as well include this here to prevent unnecessary propagation of a web3 instance
export const isBN = object => object && object.constructor && object.constructor.isBN && object.constructor.isBN(object)

export const fixReturnValues = (returnValues) => {
  const reducedValues = Object.entries(returnValues)
    .filter(entry => isNaN(entry[0]))
    .reduce((acc, [name, value]) => {
      if (isBN(value) || isBigNumber(value)) {
        value = value.toString()
      }
      acc.push([name, value])
      return acc
    }, [])
  return Object.fromEntries(reducedValues)
}

export const handleErrorStub = (error = new Error()) => { console.log(error); return error }

export const isFalseyOrPromise = (maybePromise) => !maybePromise || (typeof maybePromise === 'object' && typeof maybePromise.then === 'function')

export const valueAdder = (setter) => (value) => { console.info(value); setter((values) => [...values, value]) }
export const errorAdder = (setter) => (error) => { console.error(error); setter((errors) => [...errors, error]) }
