const getAbiItemModel = (abiModel, method) => abiModel.abi.methods[method]
const getAbiItem = (abiModel, method) => abiModel.abi.methods[method].abiItem
export const methodExists = (abiModel, method) => !!getAbiItemModel(abiModel, method)
export const methodIsPayable = (abiModel, method) => !!getAbiItem(abiModel, method).payable
// todo: figure out how to handle overloading
export const getParams = (abiModel, method) => {
  const { abiItem } = getAbiItemModel(abiModel, method)
  const { inputs } = abiItem
  const params = inputs.map((input) => ({ ...input, value: '' })) // todo: fix initial value to depend on the input type?
  return params
}
