import React, { useContext, useState, useEffect } from 'react'
import ipfsContext from '../../../context/ipfs.jsx'
import ethereumContext from '../../../context/ethereum.jsx'

const WeatherReport = (props) => {
  const { message = {} } = props
  const { ipfs } = useContext(ipfsContext)
  const { utils } = useContext(ethereumContext)
  const [report, setReport] = useState(null)
  useEffect(() => {
    const getWeatherFromIpfs = async (message = {}) => {
      const ipfsHash = utils.hexToAscii(message.payload)
      try {
        const rawReport = await ipfs.cat(ipfsHash)
        const report = rawReport.toString()
        setReport(report)
      } catch (error) {
        console.error(ipfsHash, error)
        setReport(null)
      }
    }
    if (!report) {
      getWeatherFromIpfs(message)
    }
  }, [message, ipfs, utils, report])

  return <li>{report}</li>
}

export default WeatherReport
