import React from 'react'

const KeyPairList = (props) => {
  const { keyPairs = [], showKeyPairId = false, showPublicKey = true, showPrivateKey = false } = props

  if (!keyPairs.length) {
    return (
      <ul>
        <li>No key pairs.</li>
      </ul>
    )
  }

  return (
    <ul>
      {
        keyPairs.map((keyPair, index) => (
          <li key={index}>
            {showKeyPairId ? <>Key id: <pre>{keyPair.id}</pre></> : null}
            {showPublicKey ? <>Public key: <pre>{keyPair.publicKey}</pre></> : null}
            {showPrivateKey ? <>Private key: <pre>{keyPair.privateKey}</pre></> : null}
          </li>
        ))
      }
    </ul>
  )
}

export default KeyPairList
