import React, { useReducer, createContext, useContext, useState } from 'react'
import ethereumContext from './ethereum.jsx'

const createContractInstance = (eth, jsonInterface, address, options, errorHandler) => {
  try {
    return new eth.Contract(jsonInterface, address, options)
  } catch (error) {
    errorHandler(error)
  }
}

const init = ({ authEth, eventEth, jsonInterface, address, options, errorHandler }) => {
  return {
    jsonInterface,
    address,
    options,
    authContract: createContractInstance(authEth, jsonInterface, address, options, errorHandler),
    eventContract: createContractInstance(eventEth, jsonInterface, address, options, errorHandler),
    errorHandler
  }
}

const initArg = (authEth, eventEth, jsonInterface, address, options, errorHandler) => (
  { authEth, eventEth, jsonInterface, address, options, errorHandler }
)

const reducer = (state, action) => {
  const { type, payload } = action
  const { authEth, eventEth, ...createArgs } = payload
  const {
    jsonInterface = state.jsonInterface,
    address = state.address,
    options = state.options,
    errorHandler = state.errorHandler
  } = createArgs
  const authContract = createContractInstance(authEth, jsonInterface, address, options, errorHandler)
  const eventContract = createContractInstance(eventEth, jsonInterface, address, options, errorHandler)
  switch (type) {
    case 'setJsonInterface': return { ...state, jsonInterface, authContract, eventContract }
    case 'setAddress': return { ...state, address, authContract, eventContract }
    case 'setOptions': return { ...state, options, authContract, eventContract }
    case 'setErrorHandler': return { ...state, errorHandler, authContract, eventContract }
    case 'reset': return init(payload)
    default: throw new Error('unexpected action')
  }
}

const context = createContext()

export const ContractProvider = (props) => {
  const { jsonInterface, address, options, onError, children } = props
  const { authEth, eventEth } = useContext(ethereumContext)

  const [contractErrors, setContractErrors] = useState([])
  const addContractError = (error) => setContractErrors((errors) => [...errors, error])
  const errorHandler = (error) => { addContractError(error); onError && onError(error) }

  const [state, dispatch] = useReducer(reducer, initArg(authEth, eventEth, jsonInterface, address, options, errorHandler), init)
  const setJsonInterface = (jsonInterface) => dispatch({ type: 'setJsonInterface', payload: { authEth, eventEth, jsonInterface } })
  const setAddress = (address) => dispatch({ type: 'setAddress', payload: { authEth, eventEth, address } })
  const setOptions = (options) => dispatch({ type: 'setOptions', payload: { authEth, eventEth, options } })
  const setErrorHandler = (errorHandler) => dispatch({ type: 'setErrorHandler', payload: { authEth, eventEth, errorHandler } })

  // todo: should be an error boundary ?
  if (!state.jsonInterface || !state.address) {
    return (
      <>
        Contract Context Missing <br />
        <label>Address: <input value={address} onChange={(event) => { event.persist(); setAddress(event.target.value) }} /></label><br />
        <label>JSON Interface: <input value={jsonInterface} onChange={(event) => { event.persist(); setJsonInterface(JSON.parse(event.target.value)) }} /></label>
      </>
    )
  }

  // todo: replace when error boundary is implemented
  // if (!state.authContract || !state.eventContract) { return null }

  return (
    <context.Provider value={{ ...state, setJsonInterface, setAddress, setOptions, setErrorHandler, contractErrors, addContractError }}>
      {children}
    </context.Provider>
  )
}

export const ContractConsumer = context.Consumer

export default context
