import React, { useContext, useState } from 'react'
import ethereumContext from '../../../context/ethereum.jsx'
import KeyPairList from './KeyPairList.jsx'

const WhisperKeyPairGenerator = (props) => {
  const { shh, shhErrors, addShhError } = useContext(ethereumContext)
  const [keyPairs, setKeyPairs] = useState([])

  const generateKeyPair = async () => {
    try {
      const id = await shh.newKeyPair()
      const publicKey = await shh.getPublicKey(id)
      const privateKey = await shh.getPrivateKey(id)
      const keyPair = { id, publicKey, privateKey }

      setKeyPairs((keyPairs) => [...keyPairs, keyPair])
    } catch (error) {
      addShhError(error)
    }
  }

  return (
    <div>
      <button onClick={() => generateKeyPair()} >Generate keypair</button>
      {shhErrors.length ? <pre>{shhErrors[shhErrors.length - 1].message}</pre> : null}
      <KeyPairList keyPairs={keyPairs} showKeyPairId />
    </div>
  )
}

export default WhisperKeyPairGenerator
