import React, { useContext, useState, useRef } from 'react'
import fileReaderPullStream from 'pull-file-reader'
import ipfsContext from '../../context/ipfs.jsx'
import IpfsFileList from './FileList.jsx'

const IpfsFileUpload = (props) => {
  const { ipfs } = useContext(ipfsContext)
  const [outboundFiles, setOutboundFiles] = useState([])
  const [uploadedFiles, setUploadedFiles] = useState([])
  const [uploadError, setUploadError] = useState('')
  const [uploading, setUploading] = useState(false)
  const formRef = useRef()

  const captureFiles = (event) => {
    event.stopPropagation()
    event.preventDefault()
    const files = Array.from(event.target.files).map((file) => ({ path: file.name, content: fileReaderPullStream(file) }))
    setOutboundFiles(files)
    setUploadError('')
  }

  const sendFiles = async (event) => {
    event.stopPropagation()
    event.preventDefault()
    setUploading(true)
    try {
      const files = await ipfs.add(outboundFiles) // infura http api does not seem to support multiple files
      setUploadedFiles([...files, ...uploadedFiles])
      formRef.current.reset()
    } catch (error) {
      setUploadError(error.message)
    }
    setUploading(false)
  }

  return (
    <>
      <form ref={formRef}>
        <input type='file' onChange={captureFiles} onReset={() => setOutboundFiles([])} />
        <button onClick={sendFiles} disabled={!outboundFiles.length || uploading}>Upload to IPFS</button>
      </form>
      {uploadError && <pre>{uploadError}</pre>}
      <h3>Uploaded File List</h3>
      <IpfsFileList files={uploadedFiles} />
    </>
  )
}

export default IpfsFileUpload
