import React from 'react'

const ObjectPropertyList = ({ object, ordered = false }) => {
  const items = Object.entries(object).map(([name, value], index) => (<li key={index}>{name}: {JSON.stringify(value)} </li>))
  if (ordered) {
    return <ol>{items}</ol>
  } else {
    return <ul>{items}</ul>
  }
}

export default ObjectPropertyList
