import React from 'react'
import { ETHEREUM_HTTP_CONFIG, ETHEREUM_WEBSOCKET_CONFIG, ETHEREUM_WHISPER_CONFIG, IPFS_OPTIONS, ROUTER_CONTRACT_CONFIG, ROUTER_CONTRACT_EVENTS } from './constants.js'
import { EthereumProvider } from './context/ethereum.jsx'
import { IpfsProvider } from './context/ipfs.jsx'
import { ContractProvider } from './context/contract.jsx'
import IpfsFileUpload from './components/ipfs/FileUpload.jsx'
import EthereumContractEventList from './components/ethereum/contract/EventList.jsx'
import EthereumContractMethodForm from './components/ethereum/contract/MethodForm.jsx'
import WhisperKeypairGenerator from './components/ethereum/whisper/KeyPairGenerator.jsx'
import WhisperMessageList from './components/ethereum/whisper/MessageList.jsx'
import WeatherReport from './components/ethereum/whisper/WeatherReport.jsx'
import HorizonClusterNotification from './components/ethereum/contract/HorizonClusterNotification.jsx'
import './App.css'

function App () {
  return (
    <EthereumProvider authConfig={ETHEREUM_HTTP_CONFIG} eventConfig={ETHEREUM_WEBSOCKET_CONFIG} shhConfig={ETHEREUM_WHISPER_CONFIG}>
      <IpfsProvider options={IPFS_OPTIONS}>
        <div className='App'>
          <h1>Horizon</h1>
          <div>
            <h2>IPFS File Upload</h2>
            <IpfsFileUpload />
          </div>
          <div>
            <h2>Forward Contract</h2>
            <ContractProvider name='Forward'>
              <EthereumContractMethodForm method='forward' sendTx />
            </ContractProvider>
          </div>
          <div>
            <h2>Router Contract</h2>
            <ContractProvider {...ROUTER_CONTRACT_CONFIG} name='Router'>
              <h3>Event List</h3>
              <EthereumContractEventList events={ROUTER_CONTRACT_EVENTS} eventComponent={HorizonClusterNotification} />
            </ContractProvider>
          </div>
          <div>
            <h2>Whisper</h2>
            <h3>Key Pair Generator</h3>
            <WhisperKeypairGenerator />
            <h3>Message List</h3>
            <WhisperMessageList msgComponent={WeatherReport} />
          </div>
        </div>
      </IpfsProvider>
    </EthereumProvider>
  )
}

export default App
