import React, { useContext, useState, useEffect } from 'react'
import ipfsContext from '../../../context/ipfs.jsx'
import ObjectPropertyList from '../../ObjectPropertyList.jsx'

const HorizonClusterNotification = (props) => {
  const { event } = props
  const { location, price, specs: specshash, abihash } = props.event.returnValues
  const { ipfs } = useContext(ipfsContext)
  const [jsonInterface, setJsonInterface] = useState('')
  const [specs, setSpecs] = useState(null)

  useEffect(() => {
    const getJsonInterface = async () => {
      try {
        const rawJsonInterface = await ipfs.cat(abihash)
        const jsonInterface = rawJsonInterface.toString()
        setJsonInterface(jsonInterface)
      } catch (error) {
        console.error(abihash, error)
        setJsonInterface(null)
      }
    }
    const getSpecs = async () => {
      try {
        const rawSpecsJson = await ipfs.cat(specshash)
        const specs = JSON.parse(rawSpecsJson)
        setSpecs(specs)
      } catch (error) {
        console.log(specshash, error)
        setSpecs(null)
      }
    }
    if (!jsonInterface) {
      getJsonInterface()
    }
    if (!specs) {
      getSpecs()
    }
  }, [jsonInterface, specs, ipfs, abihash, specshash])

  return (
    <li>
      {event.event}
      <ul>
        <li><label>specs: {specshash} {specs ? <ObjectPropertyList object={specs} name='' /> : null }</label></li>
        <li><label>price: {price}</label></li>
        <li><label>jsonInterface: {abihash} {jsonInterface ? <ul><li><input value={jsonInterface} disabled /></li></ul> : null}</label></li>
        <li><label>address: {location}</label></li>
      </ul>
    </li>
  )
}

export default HorizonClusterNotification
